          .navbar__content nav ul li a:hover:before, .navbar__content nav ul li a:focus:before {
            transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.3s; }
          .navbar__content nav ul li a:hover:after, .navbar__content nav ul li a:focus:after {
