  position: relative;
  display: grid;
  place-items: center;
  width: var(--size);
  height: var(--size);
  cursor: pointer; }
  label .hamburger {
    position: absolute;
    display: grid;
    place-items: center;
    width: 32px;
    height: 32px;
    top: 10px;
    display: none;
    z-index: 1000; }
    label .hamburger::before,
    label .hamburger .center, label .hamburger::after {
      --tx: 0;
      --rt: 0deg;
      content: "";
      position: absolute;
      width: 100%;
      height: var(--weight);
      background: var(--primary);
      transform-origin: right;
      transition: var(--bounce) 0.1s; }
    label .hamburger .center {
      right: 0;
      transition: var(--bounce), width var(--ease);
      transition-delay: 0.3s; }
    label .hamburger::before {
      top: 0;
      transform: translate(var(--tx), 100%) rotate(var(--rt)); }
    label .hamburger::after {
      bottom: 0;
      transform: translate(var(--tx), -100%) rotate(var(--rt)); }

#toggle:checked ~ label .hamburger::before, #toggle:checked ~ label .hamburger::after {
  --tx: -15%;
  --rt: 45deg;
  transition: var(--bounce) 0.1s; }

#toggle:checked ~ label .hamburger::before {
  --ty: 15%;
  --rt: -45deg; }

#toggle:checked ~ label .hamburger .center {
  width: 0;
  right: calc(var(--size) * -0.16);
  transition: width var(--ease), right var(--bounce); }

#toggle:checked ~ .mobile-nav {
  transform: translateY(0);
  opacity: 1; }

